html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-size: 14px;
  font-weight: 400;
  letter-spacing: 0;
  line-height: 1.28581;
  text-transform: none;
  color: #1c2127;
  font-family: -apple-system, "BlinkMacSystemFont", "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Open Sans", "Helvetica Neue", "blueprint-icons-16", sans-serif;
}

p {
  margin-bottom: 10px;
  margin-top: 0;
}

small {
  font-size: 12px;
}

strong {
  font-weight: 600;
}

::selection {
  background: rgba(125, 188, 255, 0.6);
}

